@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

*{
    box-sizing: border-box;
    font-family: 'Noto Sans KR', sans-serif;
}

body{
    margin: 0;
    padding: 0;
    background-color: #eff3f7;
    height: 100%;
}

.container{
    background-color: white;
    width:80%;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 30px;
}
.url_box{
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    width: 1000px;
    height: 50px;


}


#search{
    margin-left:60px;
    width: 800px;
    height: 50px;
    border: none;
    padding: 20px;
    border: 1px solid #ababab;
    border-radius: 30px;
    position: relative;

}

.search_btn{
    border: none;
    background-color: #3498db;
    color: white;
    width: 70px;
    height: 50px;
    border-radius: 30px;
    margin-left: 10px;
    cursor: pointer;
}

.listT{
    margin: 0 70px;
    margin-top: 100px;
}

.comment_list{
    font-weight: bolder;
    font-size: 20px;
    margin-bottom: 10px;
}

.comments{
    margin-top: 10px;
}